import React from "react"
import { Heading } from "../common/Heading"
import { about } from "../data/dummydata"
import TypeWriterEffect from "react-typewriter-effect"

export const About = () => {
  return (
    <>
      <section className='about'>
        <div className='container flex'>
          {about.map((val) => (
            <>
              <div className='left' data-aos='fade-down-right'>
                <img src={val.cover} alt='' />
              </div>
              <TypeWriterEffect
              textStyle={{ fontFamily: 'Open Sans', fontSize: 18 }} 
              startDelay={100}
              cursorColor="black"
              text="Tokoh superhero ini udah ada sebenarnya di era 30-50an yang diprakarsai oleh Marvel dan DC. Makanya di Indonesia juga ikutan membuat karakter superhero yg terinspirasi dari superhero2 dari Marvel dan DC. Contohnya Gundala dan Maze diciptakan oleh Hasmi, lalu ada Godam dan Aquanus yg diciptakan oleh Wid NS yg semuanya di thn 1968-1969. Setelah itu banyak juga superhero yang bermunculan yang namanya sepertinya cukup dikenal di kalangan masyarakat kelas menengah kebawah seperti Caroq, Jin Kartubi, Kalong, Sembrani dsb. "
              typeSpeed={50}
          />
            </>
          ))}
        </div>
      </section>
    </>
  )
}